<template>
    <ac-layout>
        <ac-button @tap="defaultToast" type="primary">Toast</ac-button>
        <ac-button @tap="successToast" type="primary">Toast</ac-button>
        <ac-button @tap="customToast" type="primary">Toast3</ac-button>
        <ac-button @tap="loadingToast" type="primary"></ac-button>
        <ac-button @tap="hideToast" type="primary">Toast</ac-button>
    </ac-layout>

</template>

<script>
    var util = require('@/utils/util')
    var playTimeInterval
    var recordTimeInterval
    export default {
        config: {
            navigationBarTitleText: ''
        },
        data() {
            return {

            }
        },
        methods: {
          defaultToast(){
            appcan.showToast({ title: 'Toast' })
          },
          successToast(){
            appcan.showToast({ title: 'Toast', icon: 'success' })
          },
          customToast(){
            appcan.showToast({ title: 'Toast3', icon: 'success', duration: 3000 })
          },
          loadingToast(){
            appcan.showToast({ title: '', icon: 'loading' })
          },
          hideToast(){
            appcan.hideToast()
          }
        }
    }
</script>

<style scoped lang="less">


</style>



