<template>
    <ac-layout>

        <ac-view class="win">required</ac-view>
        <ac-block class="input-demo">
            <ac-input required v-model="input1" max="20" placeholder=""></ac-input>
        </ac-block>

        <ac-view class="win">password</ac-view>
        <ac-block class="input-demo">
            <ac-input type="password" v-model="input2" placeholder=""></ac-input>
        </ac-block>

        <ac-view class="win"> -  - </ac-view>
        <ac-block class="input-demo">
            <ac-input v-model="input3" regex="email" placeholder=""></ac-input>
        </ac-block>

        <ac-view class="win"> -  - </ac-view>
        <ac-block class="input-demo">
            <ac-input v-model="input4" regex="mobile" placeholder=""></ac-input>
        </ac-block>

        <ac-view class="win"> -  - </ac-view>
        <ac-block class="input-demo">
            <ac-input v-model="input5" regex="bankcard" placeholder=""></ac-input>
        </ac-block>

        <ac-view class="win"> -  - QQ</ac-view>
        <ac-block class="input-demo">
            <ac-input v-model="input6" regex="^\d{5,12}$" placeholder="QQ"></ac-input>
        </ac-block>

        <ac-view class="win"></ac-view>
        <ac-block class="input-demo">
            <ac-input v-model="input7" required :showClearIcon="false" placeholder=""></ac-input>
        </ac-block>

        <ac-view class="win"></ac-view>
        <ac-block class="input-demo">
            <ac-input v-model="input8" required :showSuccessIcon="false" :showErrorIcon="false" regex="mobile"
                      placeholder=""></ac-input>
        </ac-block>

        <ac-view class="win"></ac-view>
        <ac-block class="input-demo">
            <ac-input v-model="input9" ref="input9" required regex="mobile" placeholder=""></ac-input>
        </ac-block>

        <ac-view style="color:#F00;padding: 0 .3rem;" v-html="result"></ac-view>
        <ac-view class="win">
            <ac-button @click.native="clickHander"></ac-button>
            <ac-button @tap="getUrl"></ac-button>
        </ac-view>
    </ac-layout>

</template>

<script type="text/babel">
  export default {
    config: {
      navigationBarTitleText: "Input"
    },
    data() {
      return {
        input1: '',
        input2: '',
        input3: 'appcanui@qq.com',
        input4: '',
        input5: '',
        input6: '',
        input7: '',
        input8: '',
        input9: '',
        result: ''
      }
    },
    methods: {
      clickHander() {
        const input = this.$refs.input9;
        this.result = `{<br />&nbsp;&nbsp;valid${input.valid}<br />&nbsp;&nbsp;errorMes'${input.errorMsg}'<br />&nbsp;&nbsp;errorCode'${input.errorCode}'<br />}`;
      },
      getUrl(){
        alert(JSON.stringify(appcan.getUrlQuery()))

      },
    },
    mounted(){
    }
  }
</script>

<style lang="less" scoped>
    @import '../../common/app.less';

    .ac-button.is-full-width {
        width: 100%;
        margin-bottom: 50px;
    }

    .ac-content .win {
        padding: 10px;
        color: #999;
    }

    .input-demo {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
        align-items: center;
        width: 100%;
        border-bottom: 1px solid #ccc;
        border-top: 1px solid #ccc;
        background-color: #fff;
        padding: 4px 12px;
    }

</style>



