<template>
    <ac-layout>
        <ac-view class="content">
            <ac-row height="40" space="10">
                <ac-col span="5" vertical-align="middle">
                    
                </ac-col>
                <ac-col span="7">
                    <ac-input :value="longitude"></ac-input>
                </ac-col>
            </ac-row>
            <ac-row height="40" space="10">
                <ac-col span="5" vertical-align="middle">
                    
                </ac-col>
                <ac-col span="7">
                    <ac-input :value="latitude"></ac-input>
                </ac-col>
            </ac-row>
            <ac-row height="40" space="10">
                <ac-col span="5" vertical-align="middle">
                    
                </ac-col>
                <ac-col span="7">
                    <ac-input :value="name"></ac-input>
                </ac-col>
            </ac-row>
            <ac-row height="40" space="10">
                <ac-col span="5" vertical-align="middle">
                    
                </ac-col>
                <ac-col span="7">
                    <ac-input :value="address"></ac-input>
                </ac-col>
            </ac-row>
            <ac-button type="primary" @tap="openLocation"></ac-button>
        </ac-view>
    </ac-layout>

</template>
<script>

    export default {
        config: {
            navigationBarTitleText: ''
        },
        data() {
            return {
                longitude: 116.407495,
                latitude: 40.015434,
                name: '',
                address: '222'
            }
        },
        methods: {
            openLocation: function () {
                appcan.openLocation({
                    longitude: this.longitude,
                    latitude: this.latitude,
                    name: this.name,
                    address: this.address,
                    backgroundColor: "#3699FD",
                    success: function () {

                    },
                    fail: function (res) {
                        console.log('fail:', res)
                    }
                })

            }
        },
        mounted() {
        }
    }
</script>



