<template>
    <ac-layout>
        <ac-view class="content">
            <ac-button @tap="establish"> WebSocket </ac-button>
            <ac-button @tap="close"> WebSocket </ac-button>
            <ac-input :value="exampleText" @input="handleInputChange" placeholder="" :disabled="!open"></ac-input>
            <ac-button @tap='sendSocketMessage' type="primary" :disabled="!disabled"> WebSocket </ac-button>
            <ac-textarea :value="text" class="info"></ac-textarea>
        </ac-view>
    </ac-layout>

</template>

<script>
  const sendUrl = 'http://192.168.89.35:56717'
  export default {
    data () {
      return {
        loading: false,
        disabled: false,
        open: false,
        text: '',
        socketOpen: false,
        exampleText: '',
        socketMsgQueue: []
      }
    },
    methods: {
      handleInputChange (val) {
        this.exampleText = val
        // console.log(this.exampleText)
      },
      establish () {
        var that = this
        that.loading = true
        appcan.connectSocket({
          url: sendUrl,
          success: function (res) {
            appcan.showToast({
              title: 'WebSocket'
            })
            that.loading = false
            that.socketOpen = true
            that.disabled = true
            that.open = true

            appcan.onSocketOpen(function (res) {
              appcan.showToast({
                title: 'WebSocket'
              })
              for (var i = 0; i < that.socketMsgQueue.length; i++) {
                that.sendSocketMessage(that.socketMsgQueue[i])
              }
              that.socketMsgQueue = []
            })
            appcan.onSocketError(function (res) {
              appcan.showToast({
                title: 'WebSocket'
              })
            })
            appcan.onSocketMessage(function (res) {
              that.text += '' + res.data + '\n'
            })
          },
          fail: function (res) {
            appcan.showToast({
              title: 'WebSocket'
            })
            that.loading = false
            that.socketOpen = false
            that.disabled = false
            that.open = false
          }
        })
      },
      sendSocketMessage () {
        var that = this
        var msg = that.exampleText
        if (that.socketOpen) {
          appcan.sendSocketMessage({
            data: msg,
            success: function (res) {
              console.log(res)
            },
            fail: function (res) {
              appcan.showToast({
                title: ''
              })
            }
          })
        } else {
          that.socketMsgQueue.push(msg)
        }
      },
      close () {
        var that = this
        that.loading = true
        appcan.closeSocket({
          success: function (res) {
            that.socketOpen = false
            that.disabled = false
            that.open = false
            appcan.showToast({
              title: 'WebSocket'
            })
            appcan.onSocketClose(function (res) {
              appcan.showToast({
                title: 'WebSocket '
              })
            })
          },
          fail: function () {
            appcan.showToast({
              title: 'WebSocket '
            })
          }
        })
      }
    }
  }
</script>

<style scoped lang="less">


</style>



